export default {
	props:{
		items:{
			type: Array
		},
		name:{
			type: String,
			default: '测试'
		},
		initValue: {
			type: Number,
			default: 2
		}
	},
	data() {
		return {
			index: this.initValue
		};
	},
	methods:{
		buttonChange:function(id){
			this.index = id
			this.$emit("buttonChange",id)
		}
	}
}
